.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;

}

.dialogsItems {
    padding: 10px;
    color: gainsboro;
}


.dialogsItems .active {
    color: goldenrod;
}

.messages {
    padding: 10px;
    line-height:5;
}
.messages ::before {
    content:'';
    background-image:url("data:image/jpeg;base64,");
    width: 25px;
    height: 25px;
    position: absolute;
    background-size: cover;
    left: 1670px;
    border-radius: 30px;
    margin-top: 25px;
}

.messages .message {
    color: ghostwhite;
}

.pic img {

    width: 50px;
    height: 50px;
    border-radius: 30px;


}

.form   {
    position: inherit;

}